import React, { Component } from 'react';
import ReactMarkdown from 'react-markdown'
import {connect} from 'dva'
import {getUrlParams} from '@/utils/utils'
import {Tag, Divider} from 'antd'

import BaseContent from '@/components/BaseContent'
let mapState = store => {
  return {...store.addQuestions,...store.watchQuestions,...store.main}
}
@connect(mapState)
class QuestionsDetail extends Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }

  componentDidMount () {
    const id = getUrlParams(window.location.href).id
    console.log('aaa')
    this.props.dispatch({type: 'addQuestions/getQuestions', id})
}

  render() {
    console.log(this.props)
    const {title, questions_type_text, questions_stem, questions_answer, user_name, subject_text, exam_name} = this.props
    return (
      <div style={{display: 'flex'}}>
        <BaseContent style={{flex: 1}}>
          <div style={{marginBottom:20}}>
            <span>出题人：{user_name}</span>
            <div>
              
            </div>
          </div>
          <h3>题目信息</h3>
          <div style={{marginTop:20, marginBottom:20}}>
            <Tag color="blue">{questions_type_text}</Tag>
            <Tag color="geekblue">{subject_text}</Tag>
            <Tag color="orange">{exam_name}</Tag>
          </div>
          <h4>{title}</h4>
          <div>
            <ReactMarkdown className="react-markdown" source={questions_stem}></ReactMarkdown>
          </div>
        </BaseContent>
        <Divider type="vertical" />
        <BaseContent style={{width: 500, flexShrink: 0, flex: 'initial'}}>
          <h3>答案信息</h3>
          <ReactMarkdown className="react-markdown" source={questions_answer}></ReactMarkdown>
        </BaseContent>
      </div>
    )
  }
}

export default QuestionsDetail
